import { Suspense, lazy } from "react";
import { Routes, Route } from "react-router-dom";
import { Header } from "./components/Header";
import { Signature } from "./components/Signature";
import styled from "styled-components";

function App() {
  //  screen height    --scrolled css 
  document.documentElement.style.setProperty("--scrolled", "0");
  window.addEventListener("scroll", () => {
    if (window.innerHeight < window.scrollY) {
      document.documentElement.style.setProperty("--scrolled", "1");
    } else {
      document.documentElement.style.setProperty("--scrolled", "0");
    }
  });

  //     css height 
  let timer: ReturnType<typeof setTimeout> | null = null;
  let raf: number | null = null;

  window.addEventListener("resize", () => {
    if (!timer) resize(); //  
    else clearTimeout(timer); // timer   
    timer = setTimeout(() => {
      if (raf) cancelAnimationFrame(raf);
      if (timer) clearTimeout(timer);
      timer = null;
    }, 1000);
  });

  const vh = window.innerHeight * 0.01;
  document.documentElement.style.setProperty("--vh", `${vh}px`);

  const resize = () => {
    const vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty("--vh", `${vh}px`); // css  
    raf = requestAnimationFrame(resize);
  };

  //   
  const MainPage = lazy(() => import("./pages/MainPage/index"));
  const DetailPage = lazy(() => import("./pages/DetailPage/index"));

  return (
    <Container>
      <Header />
      <Suspense fallback={<div className="w-screen h-screen">Loading...</div>}>
        <Routes>
          <Route path="/" element={<MainPage />} />
          <Route path="/project/:id" element={<DetailPage />} />
        </Routes>
      </Suspense>
      <Signature />
    </Container>
  );
}

export default App;

const Container = styled.div`
  position: relative;
  background-color: var(--primary-bgColor);
  color: var(--primary-fontColor);
  font-weight: 100;
  font-family: Pretendard-Regular;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
`;
